<template>
	<div id="home">
		<header  v-show='!vantLoad'>
			<div class="headerMain">
				<i class='icon iconfont local'>&#xe650;</i>
				<span>温州</span>
				<div class='search'>
					<van-field v-model="value" placeholder="请输入.." />
				 	<i class="icon iconfont fdj">&#xe65c;</i>
				</div>
				<i class='icon iconfont total'>&#xe70a;</i>
			</div>
		</header>
		<div class="place"></div>
		<div class="notice" :class="{'show':!vantLoad}">
			<van-notice-bar
				text="1.一样的物品，一半的价钱！时间有限，数量有限。买得到便宜买不到吃亏！打折期间，优惠多多，低价秒杀，欲购从速。
				  	2.全场打折，优惠巨多，机不可失，失不再来..........."
				left-icon="volume-o"
			/>
		</div>
		<van-loading size="24px" v-show='vantLoad'>加载中...</van-loading>
		<div class="swiperBox" >
			<van-swipe :autoplay="3000" indicator-color="white" :show-indicators='false' @change='swiperChange'>
			  	<van-swipe-item v-for='(swiper,i) in swiperList' :key="swiper.id">
			  		<img :src="swiper.img">
			  	</van-swipe-item>
			</van-swipe>
			<div class="indicator">
			  	<div :class="{'on':currentSwiper >= index}" class="dots" v-for="(swiper,index) in swiperList" :key="swiper.id"></div>
			</div>
		</div>	
		<div class="list"  v-show='!vantLoad'>
			<ul>
				<li v-for="(val,i) in categoryList" :key="val.cateId" @click='goCategory(val.title,val.cateId)'>
					<img :src="val.img">
					<span>{{val.title}}</span>
				</li>
				<li v-for="(val,index) in categoryList" :key="val.cateId+'-2'" @click='goCategory(val.title,val.cateId)'>
					<img :src="val.img">
					<span>{{val.title}}</span>
				</li>
			</ul>
		</div>
		<div class="showImg" v-show='!vantLoad'>
			<img src="https://m.360buyimg.com/mobilecms/s376x240_jfs/t1/49601/16/12206/115887/5d91b4d5E34709952/aba2bcb4855e6e52.png!q70.jpg.dpg" alt="">
			<img src="https://m.360buyimg.com/mobilecms/s376x240_jfs/t1/32449/33/15631/174497/5cc2d86bE0289110c/9c53e25651659d43.png!q70.jpg.dpg" alt="">
		</div>
		<div class="promotion" v-show='!vantLoad'>
			<h3>优惠活动</h3>
			<div class="box">
				<ul>
					<li v-for='(val,i) in promotion' :key='val.id' :class="{'marginLeft':(i+1) % 2===0}" @click="toast(val)">
						<p class="title">{{val.title}}</p>
						<span class='desc'>{{val.ad}}</span>
						<img :src="val.img" alt="">
						<a href="">点击进入</a>
					</li>				
				</ul>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	// https://api.myjson.com/bins/gqcfu   home.json
	
	export default{
		name:'home',
		data(){
			return{
				value:'',
				swiperList:'',
				categoryList:'',
				promotion:'',
				currentSwiper:0,
				vantLoad:true
			}
		},
		created(){
			this.loadData();		
		},
		methods:{
			loadData(){
				this.$axios.get('https://api.myjson.com/bins/gqcfu').then(res=>{
					let data=res.data;
					if(data.statusCode==200){
						this.swiperList=data.swiperList;
						this.categoryList=data.categoryList;
						this.promotion=data.promotion;
						let timer=null;
						if(this.promotion){
							timer=setTimeout(()=>{
								this.vantLoad=false;
							},500)
						}
						
					}
				})
			},
			swiperChange(index){
				this.currentSwiper=index;
			},
			toast(val){
				 this.$toast({
                    message: '当前点击的是：'+val.title +'\n' +val.ad,
                  })

			},
			goCategory(title,cateId){
				sessionStorage.setItem('cateId',cateId);
				sessionStorage.setItem('cateName',title);
				this.$router.push({
					name:'category',
					params:{
						cateId:cateId
					}
				})
			}
		},
		activated(){
			document.documentElement.scrollTop=document.body.scrollTop=0;
		}
	}
</script>
<style lang='stylus' scoped>
	#home
		height:100%;
	header
		height: 1rem;
		position:fixed;
		top:0;
		left:0;
		width:100%;
		background-color:#fff;
		z-index:99;
	.place
		height:1rem;
	.van-loading
		text-align: center;
		position: fixed;
		left: 50%;
		top: 50%;
		z-index:999;
		transform: translate(-50%,-50%);
	.van-loading__text
		font-size:.32rem;
	.headerMain
		width: 93vw;
		height: inherit;
		margin:0 auto;
		font-size: .3rem;
		display: flex;
		align-items: center;
		
		.local
			color: #ffc50a;
			font-size: .48rem;
			
		.search
			width: 75%;
			display: flex;
			position: relative;
			align-items:center;

			.van-cell 
				width: 100%;
				height: .64rem;
				border-radius: 15px;
				background-color: #f5f5f5;
				padding: 4px 16px;
				margin-left: .2rem;
		
			.fdj
				position: absolute;
				right: .35rem;
				color: #c0c0c0;
				font-size: .36rem;
		
		.total
			font-size: .46rem;
			margin-left: .2rem;	
	.notice
		opacity:0;
		transition:.7s;
		&.show
			opacity:1;
	.van-notice-bar
		width:93vw;
		margin:0 auto;
		font-size:.28rem;
		line-height:.48rem;
		box-sizing:border-box;
		padding:0 .2rem;
		height:.8rem;	
		>>>.van-notice-bar__wrap
			height:.48rem;
	.swiperBox
		position:relative;
		margin-top:.1rem;
		.indicator 
			position: absolute;
			bottom: .2rem;
			left: .4rem;
			background-color: rgba(255, 255, 255, 0.4);
			width: 1.5rem;
			height:0.06rem;
			border-radius: .1rem;
			overflow: hidden;
			display: flex;

			.dots 
				width:0;
				background-color: rgba(255, 255, 255, 1);
				transition: all 0.3s ease-out;

				&.on 
					width: (100%/3);
	.van-swipe
		width:92vw!important;
		margin:.1rem auto;
		border-radius: .13rem;
		height:3rem;
		overflow:hidden;
	.van-swipe-item
		img
			max-width:99.9%;
			border-radius:.18rem;
			height:inherit;

	.list
		ul
			display:flex;
			width: 92vw;
			margin:0 auto;
			flex-wrap: wrap;
			border-bottom: solid 1px #f6f6f6;
			padding-bottom:.1rem;
	
	.list 
		li
			font-size: .28rem;
			width: 25%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin-bottom: .1rem;

	.list 
		li 
			img
				width: .86rem;
				height: .86rem;
				margin:.18rem 0;
	.showImg
		width:92vw;
		height:2.4rem;
		margin:0 auto;
		margin-top:.2rem;
		img
			float:left;
			max-width:50%;
		img:nth-of-type(1)
			border-top-left-radius: .13rem;
			border-bottom-left-radius: .13rem;
		img:nth-of-type(2)
			border-top-right-radius: .13rem;
			border-bottom-right-radius: .13rem;
	.promotion
		width: 92vw;
		margin:0 auto;
		overflow:hidden;
		margin-bottom:2rem;
		
		.box
			overflow:hidden;
		h3
			font-size:.36rem;
			margin:.24rem 0;	
		li
			width: 48%;
			background-color:#ecf9f9;
			font-size:.3rem;	
			float:left;
			margin-bottom:.2rem;
			border-radius:8px;
			position:relative;
			height:2rem;
			&.marginLeft
				margin-left:4%;	
			img
				width:1.48rem;
				height:1.48rem;
				right:.1rem;
				bottom:.3rem
			
			.title
				font-size:.3rem;
				top:.2rem;
				left:.2rem;
				margin:0;
				
			span
				color: #acb0b0;
				font-size:.24rem;
				margin-top:.1rem;
				top:.6rem;
				left:.2rem;
				width:45%;
			a
				bottom:.2rem;
				left:.2rem;
				font-size:.24rem;
				color: #aaa;
			img
			.title
			span
			a
				position:absolute;  
</style>


